import L from 'leaflet';

const RestaurantIcon = new L.Icon({
    iconUrl: "../assets/icons/restaurant-icon.png",
    iconAnchor: [19, 59],
    iconSize: [40, 60],
    shadowUrl: "../assets/icons/map-icon-shadow.png",
    shadowSize: [42, 35],
    shadowAnchor: [5, 28]
});

export default RestaurantIcon




