import L from 'leaflet';

const SiteIcon = new L.Icon({
    iconUrl: "../assets/icons/site-icon.png",
    iconAnchor: [19, 59],
    iconSize: [40, 60],
    shadowUrl: "../assets/icons/map-icon-shadow.png",
    shadowSize: [42, 35],
    shadowAnchor: [5, 28]
});

export default SiteIcon




