import L from 'leaflet';

const BathroomIcon = new L.Icon({
    iconUrl: "../assets/icons/bathroom-icon.png",
    iconAnchor: [19, 59],
    iconSize: [40, 60],
    shadowUrl: "../assets/icons/map-icon-shadow.png",
    shadowSize: [42, 35],
    shadowAnchor: [5, 28]
});

export default BathroomIcon


